<template>
  <!--list-content-->
  <div class="main">
    <div class="py-container">
      <!--bread-->
      <div class="bread">
        <ul class="fl sui-breadcrumb">
          <li>
            <a href="#">全部结果</a>
          </li>
        </ul>
        <ul class="fl sui-tag">
          <li
            class="with-x"
            v-show="searchParams.categoryName"
            @click="removeCategoryName"
          >
            {{ searchParams.categoryName }}<i>×</i>
          </li>
          <li
            class="with-x"
            v-show="searchParams.keyword"
            @click="removeKeyword"
          >
            {{ searchParams.keyword }}<i>×</i>
          </li>
          <li
            class="with-x"
            v-show="searchParams.trademark"
            @click="removeTradeMark"
          >
            {{ searchParams.trademark.split(":")[1] }}<i>×</i>
          </li>
          <li
            class="with-x"
            v-for="(attrValue, index) in searchParams.props"
            @click="removeAttr(index)"
          >
            {{ attrValue.split(":")[1] }}<i>×</i>
          </li>
        </ul>
      </div>
      <!--selector-->
      <div class="clearfix selector">
        <div class="type-wrap logo">
          <div class="fl key brand">品牌</div>
          <div class="value logos">
            <ul class="logo-list">
              <li
                v-for="(trademark, index) in trademarkList"
                :key="trademark.tmId"
                @click="getTradeMark(trademark.tmId, trademark.tmName)"
              >
                {{ trademark.tmName }}
              </li>
            </ul>
          </div>
          <div class="ext">
            <a href="javascript:void(0);" class="sui-btn">多选</a>
            <a href="javascript:void(0);">更多</a>
          </div>
        </div>
        <div
          class="type-wrap"
          v-for="(attr, index) in attrsList"
          :key="attr.attrId"
        >
          <div class="fl key">{{ attr.attrName }}</div>
          <div class="fl value">
            <ul class="type-list">
              <li v-for="(attrValue, index) in attr.attrValueList" :key="index">
                <a
                  @click="
                    getAttrAndAttrValue(attr.attrId, attr.attrName, attrValue)
                  "
                  >{{ attrValue }}</a
                >
              </li>
            </ul>
          </div>
          <div class="fl ext"></div>
        </div>
      </div>
      <!--details-->
      <div class="details clearfix">
        <div class="sui-navbar">
          <div class="navbar-inner filter">
            <ul class="sui-nav">
              <li :class="{ active: isOne }" @click="sort('1')">
                <a
                  >综合<span
                    :class="{
                      ' arrow-bottom': isDesc,
                      ' arrow-top': isAsc,
                      arrow: isOne,
                    }"
                  ></span
                ></a>
              </li>
              <li :class="{ active: isTwo }" @click="sort('2')">
                <a
                  >价格<span
                    :class="{
                      ' arrow-bottom': isDesc,
                      ' arrow-top': isAsc,
                      arrow: isTwo,
                    }"
                  ></span
                ></a>
              </li>
            </ul>
          </div>
        </div>
        <div class="goods-list">
          <ul class="yui3-g">
            <li
              class="yui3-u-1-5"
              v-for="(goods, index) in goodsList"
              :key="index"
            >
              <div class="list-wrap">
                <div class="p-img">
                  <router-link :to="`/detail/${goods.id}`">
                    <img :src="goods.defaultImg" />
                  </router-link>
                </div>
                <div class="price">
                  <strong>
                    <em>¥</em>
                    <i> {{ goods.price }}</i>
                  </strong>
                </div>
                <div class="attr">
                  <a target="_blank" href="item.html" :title="goods.title">{{
                    goods.title
                  }}</a>
                </div>
                <div class="commit">
                  <i class="command">已有<span>2000</span>人评价</i>
                </div>
                <div class="operate">
                  <a
                    href="success-cart.html"
                    target="_blank"
                    class="sui-btn btn-bordered btn-danger"
                    >加入购物车</a
                  >
                  <a href="javascript:void(0);" class="sui-btn btn-bordered"
                    >收藏</a
                  >
                </div>
              </div>
            </li>
          </ul>
        </div>
        <div class="fr page">
          <el-pagination
            @current-change="currentChange"
            :current-page.sync="searchParams.pageNo"
            :page-size="searchParams.pageSize"
            layout="total,  prev, pager, next, jumper"
            :total="searchList.total"
            background
            :pager-count="7"
          >
          </el-pagination>
        </div>
      </div>
      <!--hotsale-->
      <div class="clearfix hot-sale">
        <h4 class="title">热卖商品</h4>
        <div class="hot-list">
          <ul class="yui3-g">
            <li class="yui3-u-1-4">
              <div class="list-wrap">
                <div class="p-img">
                  <img src="../images/search/like_01.png" />
                </div>
                <div class="attr">
                  <em>Apple苹果iPhone 6s (A1699)</em>
                </div>
                <div class="price">
                  <strong>
                    <em>¥</em>
                    <i>4088.00</i>
                  </strong>
                </div>
                <div class="commit">
                  <i class="command">已有700人评价</i>
                </div>
              </div>
            </li>
            <li class="yui3-u-1-4">
              <div class="list-wrap">
                <div class="p-img">
                  <img src="../images/search/like_03.png" />
                </div>
                <div class="attr">
                  <em>金属A面，360°翻转，APP下单省300！</em>
                </div>
                <div class="price">
                  <strong>
                    <em>¥</em>
                    <i>4088.00</i>
                  </strong>
                </div>
                <div class="commit">
                  <i class="command">已有700人评价</i>
                </div>
              </div>
            </li>
            <li class="yui3-u-1-4">
              <div class="list-wrap">
                <div class="p-img">
                  <img src="../images/search/like_04.png" />
                </div>
                <div class="attr">
                  <em>256SSD商务大咖，完爆职场，APP下单立减200</em>
                </div>
                <div class="price">
                  <strong>
                    <em>¥</em>
                    <i>4068.00</i>
                  </strong>
                </div>
                <div class="commit">
                  <i class="command">已有20人评价</i>
                </div>
              </div>
            </li>
            <li class="yui3-u-1-4">
              <div class="list-wrap">
                <div class="p-img">
                  <img src="../images/search/like_02.png" />
                </div>
                <div class="attr">
                  <em>Apple苹果iPhone 6s (A1699)</em>
                </div>
                <div class="price">
                  <strong>
                    <em>¥</em>
                    <i>4088.00</i>
                  </strong>
                </div>
                <div class="commit">
                  <i class="command">已有700人评价</i>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { mapState } from "vuex";
export default {
  data() {
    return {
      searchParams: {
        category1Id: "", //一级分类的id
        category2Id: "", //二级分类的id
        category3Id: "", //三级分类的id
        categoryName: "", //商品的名字
        keyword: "", //用户搜索的关键字
        props: [], //商品属性的搜索条件
        trademark: "", //品牌的搜索条件
        order: "1:desc", //排序的参数 【默认初始值:1:desc】
        pageNo: 1, //当前分页器的页码  【默认初始值:1】
        pageSize: 10, //代表当前一页显示几条数据 【默认初始值:10】
      },
    };
  },
  beforeMount() {
    // [this.searchParams.category1Id,this.searchParams.category2Id,this.searchParams.category3Id,this.searchParams.keyword] =
    // [this.$route.query.category1Id,this.$route.query.category2Id,this.$route.query.category3Id,this.$route.query.keyword]
    Object.assign(this.searchParams, this.$route.query, this.$route.params);
  },
  mounted() {
    this.searchData();
  },
  methods: {
    searchData() {
      this.$store.dispatch("search/getSearchList", this.searchParams);
    },
    removeCategoryName() {
      this.searchParams.categoryName = "";
      this.$router.push({ name: "search", params: this.$route.params });
    },
    removeKeyword() {
      //清空关键字
      this.searchParams.keyword = "";
      //修改URL
      console.log(this.$route);
      this.searchData();
      this.$router.push({
        name: "search",
        query: this.$route.query,
        params: this.searchParams.keyword,
      });

      //通知兄弟组件清除关键字
    },
    getTradeMark(tmId, tmName) {
      //整理品牌相关的搜索条件
      this.searchParams.trademark = `${tmId}:${tmName}`;
      //再次发请求即可
      this.searchData();
    },
    removeTradeMark() {
      //清空关键字
      this.searchParams.trademark = "";
      this.searchData();
    },
    getAttrAndAttrValue(attrId, attrName, attrValue) {
      //整理最新的搜索的条件
      //整理为字符串
      let newProps = `${attrId}:${attrValue}:${attrName}`;

      if (this.searchParams.props.indexOf(newProps) == -1) {
        this.searchParams.props.push(newProps);
        //再次发请求，获取最新的数据展示即可
        this.searchData();
      }
    },
    removeAttr(index) {
      //删除对应的数组里面元素
      this.searchParams.props.splice(index, 1);
      //在发一次请求
      this.searchData();
    },
    sort(num) {
      let [desc, asc, newOrder] = ["desc", "asc"];
      let originSortType = this.searchParams.order.split(":")[1];
      if (num == "1") {
        newOrder = `${num}:${desc}`;
        if (originSortType == desc) {
          newOrder = `${num}:${asc}`;
        } else if (originSortType == asc) {
          newOrder = `${num}:${desc}`;
        }
      }
      if (num == "2") {
        newOrder = `${num}:${desc}`;
        if (originSortType == desc) {
          newOrder = `${num}:${asc}`;
        } else if (originSortType == asc) {
          newOrder = `${num}:${desc}`;
        }
      }
      this.searchParams.order = newOrder;
      // //获取每一次order初始值,与用户点击传递进来的flag进行判断
      // let originFlag = this.searchParams.order.split(":")[0];
      // let originSortType = this.searchParams.order.split(":")[1];
      // //准备一个新的数值，将来赋值给order
      // let newOrder = "";
      // //高亮的判断
      // if (flag == originFlag) {
      //   newOrder = `${originFlag}:${originSortType == "desc" ? "asc" : "desc"}`;
      // } else {
      //   //不是高亮的按钮
      //   newOrder = `${flag}:desc`;
      // }
      // //重新给order赋予新的数值
      // this.searchParams.order = newOrder;
      //重新发一次请求
      this.searchData();
    },
    currentChange() {
      this.searchData();
    },
  },
  watch: {
    $route() {
      //再次整理最新的商品名字参数
      // this.searchParams.category1Id = this.$route.query.category1Id;
      // this.searchParams.category2Id = this.$route.query.category2Id;
      // this.searchParams.category3Id = this.$route.query.category3Id;
      // this.searchParams.categoryName = this.$route.query.categoryName;

      //先把用户前面存储的1|2|3级别ID清除
      //发ajax的时候,属性值为undefind,甚至参数K都不携带了【10个搜索条件,可有可无的】
      this.searchParams.category1Id = undefined;
      this.searchParams.category2Id = undefined;
      this.searchParams.category3Id = undefined;
      //路由变化整理参数：手机最新的商品名字、商品1|2|3ID
      Object.assign(this.searchParams, this.$route.query, this.$route.params);
      //再次发请求
      this.getData();
    },
  },
  computed: {
    ...mapState("search", ["searchList"]),
    ...mapGetters("search", ["goodsList", "attrsList", "trademarkList"]),
    isOne: function () {
      return this.searchParams.order.split(":")[0] == 1;
    },
    isTwo: function () {
      return this.searchParams.order.split(":")[0] == 2;
    },
    isDesc: function () {
      return this.searchParams.order.indexOf("desc") == -1;
    },
    isAsc: function () {
      return this.searchParams.order.indexOf("asc") == -1;
    },
  },
};
</script>

