import { reqAddressInfo,reqShopInfo,reqSubmitOrder } from "@/api"
const state = {
    addressInfo:[],
    shopInfo:[],
    payId:''
}

const mutations = {
    GETADDRESSINFO(state,data){
        state.addressInfo = data
    },
    GETSHOPINFO(state,data){
        state.shopInfo = data
    },
    SUBMITINFO(state,data){
        state.payId = data
    }
}

const actions = {
    async getAddressInfo({commit}){
        let result = await reqAddressInfo()
        console.log(result)
        if(result.code == 200){
            commit('GETADDRESSINFO',result.data)
        }
    },
    async getShopInfo({commit}){
        let result = await reqShopInfo()
        console.log(result)
        if(result.code == 200){
            commit('GETSHOPINFO',result.data)
        }
    },
    async getSubmitOrder({commit},{tradeNo,data}){
        let result = await reqSubmitOrder(tradeNo,data)
        if (result.code == 200) {
            commit('SUBMITINFO', result.data);
            return 'ok';
        } else {
            return Promise.reject(new Error(result.message));
        }
    },
    
}

const getters = {

}

export default{
    namespaced:true,
    state,
    mutations,
    actions,
    getters
}