<template>
  <div class="cart">
    <h4>全部商品</h4>
    <div class="cart-main">
      <div class="cart-th">
        <div class="cart-th1">全部</div>
        <div class="cart-th2">商品</div>
        <div class="cart-th3">单价（元）</div>
        <div class="cart-th4">数量</div>
        <div class="cart-th5">小计（元）</div>
        <div class="cart-th6">操作</div>
      </div>
      <div class="cart-body">
        <ul
          class="cart-list"
          v-for="(cartItem, index) in cartInfoList"
          :key="cartItem.id"
        >
          <li class="cart-list-con1">
            <input
              type="checkbox"
              name="chk_list"
              @click="changeChecked(cartItem, $event)"
              :checked="cartItem.isChecked"
            />
          </li>
          <li class="cart-list-con2">
            <img :src="cartItem.imgUrl" />
            <div class="item-msg">{{ cartItem.skuName }}</div>
          </li>
          <li class="cart-list-con3">
            <div class="item-txt">&nbsp;</div>
          </li>
          <li class="cart-list-con4">
            <span class="price">{{ cartItem.cartPrice }}</span>
          </li>
          <li class="cart-list-con5">
            <a
              href="javascript:void(0)"
              class="mins"
              @click="handler('minus', cartItem)"
              >-</a
            >
            <input
              autocomplete="off"
              type="text"
              :value="cartItem.skuNum"
              minnum="1"
              class="itxt"
              @change="handler('change', cartItem, $event)"
            />
            <a
              href="javascript:void(0)"
              class="plus"
              @click="handler('add', cartItem)"
              >+</a
            >
          </li>
          <li class="cart-list-con6">
            <span class="sum">{{ cartItem.cartPrice * cartItem.skuNum }}</span>
          </li>
          <li class="cart-list-con7">
            <a href="#none" class="sindelet" @click="deleteCart(cartItem)"
              >删除</a
            >
            <br />
            <a href="#none">移到收藏</a>
          </li>
        </ul>
      </div>
    </div>
    <div class="cart-tool">
      <div class="select-all">
        <input class="chooseAll" type="checkbox" :checked="isAllChecked" @click="changeAllChecked($event)"/>
        <span>全选</span>
      </div>
      <div class="option">
        <a href="#none" @click="deleteAllCheckedCart">删除选中的商品</a>
        <a href="#none">移到我的关注</a>
        <a href="#none">清除下柜商品</a>
      </div>
      <div class="money-box">
        <div class="chosed">已选择 <span>{{isCheckedNum}}</span>件商品</div>
        <div class="sumprice">
          <em>总价（不含运费） ：</em>
          <i class="summoney">{{ total }}</i>
        </div>
        <div class="sumbtn">
          <router-link class="sum-btn" to="/trade" >结算</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import throttle from "lodash/throttle";
import debounce from "lodash/debounce";
export default {
  name: "ShopCart",
  mounted() {
    this.getData();
    console.log(this.cartInfoList);
  },
  computed: {
    ...mapGetters("shopcart", ["CartInfo"]),
    //购物车的数据
    cartInfoList() {
      return this.CartInfo.cartInfoList || [];
    },
    total() {
      return this.cartInfoList.reduce((a, b) => a + b.skuPrice * b.skuNum, 0);
    },
    isCheckedNum(){
      return this.cartInfoList.filter(item=>item.isChecked=='1').length
    },
    isAllChecked(){
      return this.cartInfoList.filter(item=>item.isChecked=='1').length===this.cartInfoList.length;
    }
  },
  methods: {
    getData() {
      this.$store.dispatch("shopcart/getShopCart");
    },
    handler(type, cartItem, e) {
      let params;
      let event = e;
      let that = this;
      //type为加号的情况
      if (type === "add") {
        (async function () {
          params = { skuId: cartItem.skuId, skuNum: 1 };
          try {
            //修改商品个数成功
            await that.$store.dispatch("detail/addOrUpdateCart", params);
            //再次获取最新的购物车的数据
            that.getData();
          } catch (error) {
            alert("修改数量失败");
          }
        })();
      } else if (type === "minus") {
        //type为减号的情况
        throttle(async function () {
          if (cartItem.skuNum > 1) {
            //整理参数:至少加入购物车的数量最低1个
            params = { skuId: cartItem.skuId, skuNum: -1 };
            //修改商品的数据
            try {
              //修改商品的个数、成功以后再次获取购物车的数据
              await that.$store.dispatch("detail/addOrUpdateCart", params);
              that.getData();
            } catch (error) {
              alert("修改数量失败");
            }
          }
        }, 2000)();
      } else if (type === "change") {
        //type为减号的情况
        debounce(async function () {
          //整理参数
          console.log(event.target.value);
          let params = { skuId: cartItem.skuId };
          //计算出SkuNum携带的数据
          let userResultValue = event.target.value * 1;
          if (isNaN(userResultValue) || userResultValue < 1) {
            params.skuNum = 0;
          } else {
            //正常情况
            params.skuNum = parseInt(userResultValue) - cartItem.skuNum;
          }
          //发请求：修改商品的个数
          try {
            //修改商品的个数、成功以后再次获取购物车的数据
            await that.$store.dispatch("detail/addOrUpdateCart", params);
            that.getData();
          } catch (error) {
            alert("错误");
          }
        }, 500)();
      }
    },
    deleteCart(cartItem) {
      let skuId = cartItem.skuId;
      try {
        this.$store.dispatch("shopcart/deleteCartById", skuId);
        this.getData();
      } catch (error) {
        alert("删除失败");
      }
    },
    async changeChecked(cartItem, e) {
      let params = {
        skuId: cartItem.skuId,
        isChecked: e.target.checked ? "1" : "0",
      };
      try {
        await this.$store.dispatch("shopcart/changeChecked", params);
        this.getData();
      } catch (error) {
        alert("错误");
      }
    },
    async changeAllChecked(e){
      let isChecked = e.target.checked ? "1" : "0";
      try {
        //await等待成功:购物车全部商品勾选状态成功以后
        await this.$store.dispatch("shopcart/allUpdateChecked", isChecked);
        this.getData();
      } catch (error) {
        alert('修改失败');
      }
    },
    async deleteAllCheckedCart(){
       try {
         //等待全部勾选商品删除以后
         await  this.$store.dispatch('shopcart/deleteAllCart');
         //再次获取购物车的数据
         this.getData();
       } catch (error) {
         alert('删除失败');
       }
    }
  },
};
</script>

<style lang="less" scoped>
.cart {
  width: 1200px;
  margin: 0 auto;

  h4 {
    margin: 9px 0;
    font-size: 14px;
    line-height: 21px;
  }

  .cart-main {
    .cart-th {
      background: #f5f5f5;
      border: 1px solid #ddd;
      padding: 10px;
      overflow: hidden;

      & > div {
        float: left;
      }

      .cart-th1 {
        width: 25%;

        input {
          vertical-align: middle;
        }

        span {
          vertical-align: middle;
        }
      }

      .cart-th2 {
        width: 25%;
      }

      .cart-th3,
      .cart-th4,
      .cart-th5,
      .cart-th6 {
        width: 12.5%;
      }
    }

    .cart-body {
      margin: 15px 0;
      border: 1px solid #ddd;

      .cart-list {
        padding: 10px;
        border-bottom: 1px solid #ddd;
        overflow: hidden;

        & > li {
          float: left;
        }

        .cart-list-con1 {
          width: 4.1667%;
        }

        .cart-list-con2 {
          width: 25%;

          img {
            width: 82px;
            height: 82px;
            float: left;
          }

          .item-msg {
            float: left;
            width: 150px;
            margin: 0 10px;
            line-height: 18px;
          }
        }

        .cart-list-con3 {
          width: 20.8333%;

          .item-txt {
            text-align: center;
          }
        }

        .cart-list-con4 {
          width: 12.5%;
        }

        .cart-list-con5 {
          width: 12.5%;

          .mins {
            border: 1px solid #ddd;
            border-right: 0;
            float: left;
            color: #666;
            width: 6px;
            text-align: center;
            padding: 8px;
          }

          input {
            border: 1px solid #ddd;
            width: 40px;
            height: 33px;
            float: left;
            text-align: center;
            font-size: 14px;
          }

          .plus {
            border: 1px solid #ddd;
            border-left: 0;
            float: left;
            color: #666;
            width: 6px;
            text-align: center;
            padding: 8px;
          }
        }

        .cart-list-con6 {
          width: 12.5%;

          .sum {
            font-size: 16px;
          }
        }

        .cart-list-con7 {
          width: 12.5%;

          a {
            color: #666;
          }
        }
      }
    }
  }

  .cart-tool {
    overflow: hidden;
    border: 1px solid #ddd;

    .select-all {
      padding: 10px;
      overflow: hidden;
      float: left;

      span {
        vertical-align: middle;
      }

      input {
        vertical-align: middle;
      }
    }

    .option {
      padding: 10px;
      overflow: hidden;
      float: left;

      a {
        float: left;
        padding: 0 10px;
        color: #666;
      }
    }

    .money-box {
      float: right;

      .chosed {
        line-height: 26px;
        float: left;
        padding: 0 10px;
      }

      .sumprice {
        width: 200px;
        line-height: 22px;
        float: left;
        padding: 0 10px;

        .summoney {
          color: #c81623;
          font-size: 16px;
        }
      }

      .sumbtn {
        float: right;

        a {
          display: block;
          position: relative;
          width: 96px;
          height: 52px;
          line-height: 52px;
          color: #fff;
          text-align: center;
          font-size: 18px;
          font-family: "Microsoft YaHei";
          background: #e1251b;
          overflow: hidden;
        }
      }
    }
  }
}
</style>