import axios from "axios";

const requests = axios.create({
    baseURL:'/mock',
    timeout:5000
})

//请求拦截器
requests.interceptors.request.use((config)=>{
    return config
})
//响应拦截器
requests.interceptors.response.use((res)=>{
    return res.data
},(err)=>{
    //响应失败的回调
    return Promise.reject(new Error('failed'))
})
export default requests