//配置路由
import Vue from "vue";
import VueRouter from 'vue-router'

import store from '@/store'
Vue.use(VueRouter)
import Home from '@/pages/Home'
import Search from '@/pages/Search'
import Login from '@/pages/Login'
import Register from '@/pages/Register'
import addCart from '@/pages/addCart'
import trade from '@/pages/trade'
import Detail from '@/pages/Detail'
import ShopCart from '@/pages/ShopCart'
import Pay from '@/pages/Pay'
import paySuccess from '@/pages/paySuccess'
import center from '@/pages/center'
import myOrder from '@/pages/center/myOrder'
import teamOrder from '@/pages/center/teamOrder'
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function(location){
    return originalPush.call(this,location).catch(err=>err)
}

const router = new VueRouter({
    routes:[
        {
            path:'*',
            redirect:"/home"
        },
        {
            path:'/home',
            component:Home,
            name:'home'
        },
        {
            path:'/search',
            component:Search,
            name:'search'
        },
        {
            path:'/login',
            component:Login,
            name:'login'
        },
        {
            path:'/register',
            component:Register,
            name:'register'
        },
        {
            path:'/addcart',
            component:addCart,
            name:'addCart'
        },
        {
            path:'/trade',
            component:trade,
            name:'trade'
        },
        {
            path:'/detail/:skuID?',
            component:Detail,
            name:'detail'
        },
        {
            path:'/shopCart',
            component:ShopCart,
            name:'shopcart'
        },
        {
            path:'/pay',
            component:Pay,
            name:'pay'
        },
        {
            path:'/paysuccess',
            component:paySuccess,
            name:'paysuccess'
        },
        {
            path:'/center',
            component:center,
            name:'center',
            children:[
                {
                    path:'teamorder',
                    component:teamOrder,
                },
                {
                    path:'myorder',
                    component:myOrder,
                },
                {
                    path:'',
                    redirect:'/center/myorder'
                }
            ]
        }
    ],
})
router.beforeEach(async(to,from,next)=>{
    //token
    let hasToken = store.state.user.token;
    //用户信息
    let hasNickName = store.state.user.nickName;
    //用户登录
    if (hasToken) {
        //用户登录了,不能去login
        if (to.path == "/login") {
            next('/home');
        } else {
            //用户登陆了,而且还有用户信息【去的并非是login】
            if (hasNickName) {
                next();
            } else {
                //用户登陆了,但是没有用户信息 
                try {
                    //发请求获取用户信息以后在放行
                    await store.dispatch('user/getUserInfo');
                    next();
                } catch (error) {
                    //用户没有信息，还携带token发请求获取用户信息【失败】
                    //token【学生证失效了】
                    //token失效:本地清空数据、服务器的token通知服务器清除
                    await store.dispatch('user/loginOut');
                    //回到登录页，重新获取一个新的学生证
                    next('/login');
                }
            }
        }
    } else {
        //用户未登录||目前的判断都是放行.将来这里会'回手掏'增加一些判断
        //用户未登录:不能进入/trade、/pay、/paysuccess、/center、/center/myorder  /center/teamorder
        let toPath = to.path;
        if (toPath.indexOf('trade') != -1 || toPath.indexOf('pay') != -1 || toPath.indexOf('center') != -1) {
            next('/login?redirect='+toPath);
        } else {
            next();
        }
    }
})
export default router



