import { reqDetailList,reqAddOrUpdateCart } from "@/api";


const state = {
    detailList:{}
}

const mutations = {
    GETDETAILLIST(state,detailList){
        state.detailList = detailList
    }
}

const actions = {
    async getDetailList({commit},skuId){
        let result = await reqDetailList(skuId)
        console.log(result)
        if(result.code == 200){
            console.log(result)
            commit('GETDETAILLIST',result.data)
        }
    },
    async addOrUpdateCart({ state, commit, dispatch }, { skuId, skuNum }){
        let result = await reqAddOrUpdateCart( skuId, skuNum )
        console.log(result)
        if(result.code == 200){
            return 'ok'
        }else{
            return Promise.reject()
        }
    }
    
}

const getters = {
    //面包屑的数据
    categoryView() {
        //研究这个问题:
        //起始状态:state.detailInfo起始状态空对象,空对象.categoryView->undefined
        //当服务器数据回来之后state.detailInfo,并非空对象,获取的即为服务器返回的数据{7个K}
        //当前属性值:服务器的数据有值，用服务器的。服务器数据没有回来至少有一个空对象兜底【不能undefined兜底】
        return state.detailList.categoryView || {}
   },
   //商品信息的数据
   skuInfo() {
        return state.detailList.skuInfo || {}
   },
   //商品销售属性列表的数据
   spuSaleAttrList() {
        return state.detailList.spuSaleAttrList || []
   }
}

export default{
    namespaced:true,
    state,
    mutations,
    actions,
    getters
}